<template>
    <div class="weather">
        <div class="widget" ref='widget' :style='blockPosition'>
            <!-- <span class="draggable">(draggable)</span> -->
            <span class="probablyCountry-container">Did you mean
                <span @click="getWeather('probably')" class="probablyCountry">{{ probablyCountry }}?</span>
            </span>
            <div class="left-panel panel">
                <div class="city">
                    <input class="input-country" type="text" v-model="userCoutry" v-on:input="changecountry($event)">
                    <button class="showWeather" @click='getWeather("input")'>Search</button>
                </div>
                <div class="info">
                    <div class="weather-info">
                        <span>{{ weather }}</span>
                        <img :src="currentWeatherImg">
                    </div>

                    <div class="temp">
                        <span>Temp {{ temp }}&deg; </span>
                        <span class="temp-symbol" v-bind:class="{ active: !isCelsius }" @click='changeTemp("fahrenheit")'>F</span>
                        <span> | </span>
                        <span class="temp-symbol" v-bind:class="{ active: isCelsius }" @click='changeTemp("celsius")'>&#8451</span>
                    </div>
                </div>
            </div>
            <span @click='showMoreWeather' class="more-btn">More</span>
        </div>
        <more @closePopup='closePopup' :weather='moreWeather' v-if='isShowMoreWeather' class="more-popup"></more>
    </div>
</template>

<script>
import weatherService from './../../shared/services/weather.service';
import autocompleteService from './../../shared/services/autocomplete.service';
import more from './../../components/more-weather/more-weather'
import _ from 'lodash'

export default {
    name: 'Weather',
    components: {
        more,
    },
    data() {
        return {
            temp: '',
            location: '',
            userCoutry: 'London',
            currentWeatherImg: '',
            weather: '',
            isMouseDown: false,
            block: null,
            probablyCountry: 'London',
            blockPosition: {
                margin: 'auto auto'
            },
            items: ['London', 'Chicago', 'New York'],
            selected: '',
            moreWeather: null,
            isShowMoreWeather: false,
            isCelsius: true,
        }
    },
    created() {
        this.isMouseDown = false;
        weatherService.getWeatherByCountry(this.userCoutry).then((res) => {
            this.moreWeather = res.data;
            this.temp = res.data.main.temp;
            this.location = res.data.name;
            this.userCoutry = res.data.name;

            this.currentWeatherImg = `http://openweathermap.org/img/wn/${res.data.weather[0].icon}@2x.png`
            this.weather = res.data.weather[0].description
        });
    },
    methods: {
        changecountry:_.debounce(function(event) {
            const value = event.target.value;
            autocompleteService.getDataForAutocomplete(value).then((res) => {
                let fullCountry = res.data.predictions[0].description;
                let country = fullCountry.split(',');
                this.probablyCountry = country[0];
            }, (err) => {
                console.log(err)
            })
        }, 500),

        getWeather(type) {
            let value;

            if (type === 'input') {
                value = this.userCoutry;
            } else {
                value = this.probablyCountry;
            }

            weatherService.getWeatherByCountry(value).then((res) => {
                this.moreWeather = res.data;
                this.temp = res.data.main.temp;
                this.location = res.data.name;
                this.userCoutry = res.data.name;
                
                this.currentWeatherImg = `http://openweathermap.org/img/wn/${res.data.weather[0].icon}@2x.png`
                this.weather = res.data.weather[0].description
            });
        },

        // Widget moveable functions
        mouseDown(e) {
            // let x;
            // let y;
            // let blockX = this.$refs['widget'].getBoundingClientRect().left;
            // let blockY = this.$refs['widget'].getBoundingClientRect().top;

            // if (e.clientY && e.clientX) {
            //     x = e.clientX;
            //     y = e.clientY
            // } else if (e.touches[0].clientX && e.touches[0].clientY) {
            //     x = e.touches[0].clientX;
            //     y = e.touches[0].clientY
            // }

            // this.blockPosition = {
            //     left: x + (x - blockX) + 'px',
            //     top: y + (y - blockY) + 'px',
            // }

            // this.isMouseDown = true;
        },

        mouseUp() {
            //this.isMouseDown = false;
        },

        changePosition(e) {
            // if (this.isMouseDown) {
            //     let x;
            //     let y;
            //     let blockX = this.$refs['widget'].getBoundingClientRect().left;
            //     let blockY = this.$refs['widget'].getBoundingClientRect().top;
                
            //     if (e.clientY && e.clientX) {
            //         x = e.clientX;
            //         y = e.clientY;
            //     } else if (e.touches[0].clientX && e.touches[0].clientY) {
            //         x = e.touches[0].clientX;
            //         y = e.touches[0].clientY
            //     }

            //     this.blockPosition.left = x + (x - blockX) + 'px';
            //     this.blockPosition.top = y + (y - blockY) + 'px';
            // }
        },

        changeTemp(temp) {
            let value = this.userCoutry;

            if (temp === 'fahrenheit') {
                this.isCelsius = false;
                weatherService.getWeatherByCountry(value).then((res) => {
                    this.temp = res.data.main.temp * 1.8 + 32;
                });
            } else {
                this.isCelsius = true;
                weatherService.getWeatherByCountry(value).then((res) => {
                    this.temp = res.data.main.temp;
                });
            }
        },

        showMoreWeather() {
            this.isShowMoreWeather = true;
        },

        closePopup() {
            this.isShowMoreWeather = false;
        }
    },
}
</script>

<style scoped>
    .weather {
        position: absolute;
        display: flex;
        /* align-items: center;
        justify-content: center; */
        width: 100%;
        height: 100%;
        top: 0;
        overflow: hidden;
    }

    .showWeather {
        width: 100%;
        background-color: transparent;
        outline: none;
        border: none;
        margin-top: 5px;
        border: 1px solid black;
    }

    .probablyCountry-container {
        font-size: 14px;
    }

    .probablyCountry {
        color: blue;
    }

    div.widget {
        display: flex;
        position: relative;
        flex-direction: column;
        width: 550px;
        height: 200px;
        /* margin: auto auto; */
        background-color: #fcfdfd;
        border-radius: 9px;
        padding: 25px;
        padding-right: 30px;
        box-shadow: 0px 31px 35px -26px #080c21;
        z-index: 9;
    }

    div.left-panel {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 100%;
    }

    .more-btn {
        position: absolute;
        bottom: 15px;
        right: 15px;
        cursor: pointer;
    }

    .more-popup {
        position: absolute;
        top: 0; left: 0; right: 0; bottom: 0;
        z-index: 999;
    }

    .info {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    div.city {
        position: relative;
        font-size: 18px;
        font-weight: bold;
        text-transform: uppercase;
        padding-top: 5px;
        width: 30%;
        color: rgba(0,0,0,0.7);
    }

    div.temp,
    div.weather-info {
        font-size: 25px;
        color: rgba(0,0,0,0.9);
        font-weight: 100;
    }

    div.right-panel {
        position: absolute;
        float: right;
        top: 0;
    }

    .temp-symbol {
        cursor: pointer;
    }

    .active {
        color: blue;
    }

    .input-country {
        outline: none;
        border: none;
        font-size: 18px;
        width: 100%;
        border-bottom: 1px solid black;
    }

    @media (max-width: 650px) {
        div.widget {
            width: 96%;
            height: 300px;
        }

        div.city {
            width: 40%;
        }

        div.left-panel {
            flex-direction: column;
        }
    }

    @media (max-width: 350px) {
        div.widget {
            width: 96%;
            height: 300px;
        }
    }
</style>